import React from 'react';
import './ContentDesign.css';
import image from '../../../images/arrow_back-24px.svg';
import imageTwo from '../../../images/arrow_forward-24px.svg';

function ContentDesign(props){
	return(
		<div className="content-design">
			<div className="home column text-align-right">
				<h2 className="title-design">{props.title}</h2>
				<span className="font-medium space-item-big-subtitle">{props.location}</span>
				<div className="section-arrow row-position justify-end">
					{props.step > 1 && 
						<img src={image} alt="icono ilustracion" className="arrow cursor" onClick={props.stepPrev} />
					}
					{props.step < props.max ?
						<img src={imageTwo} alt="icono ilustracion" className="cursor" onClick={props.stepContinue}/>
					: <div className="space-arrow"></div>
					}
				</div>
				<span className="font-medium space-item-big-name-img">{props.description.nameImg}</span>
				<span className="font-medium space-item-big-description-img">{props.description.descriptionImg}</span>
			</div>
		</div>
	)
}

export default ContentDesign